import { Link } from 'react-router-dom';
import React from 'react';
import logo from '../../logo.svg';
import './Navbar.css';

function Sidebar() {
  return (
    <nav className="d-sm-block d-md-none bg-light fixed-top">
        <ul className="navbar-nav ml-auto d-flex flex-row">

            <Link to={'/'}>
                <img src={logo} style={{height:'5vh',marginTop:'6px'}} alt="Logo"/>
                </Link>
            <Link to={'/'} className="margin-1">
                Home
            </Link>
            {/* <Link to={'/posts'} className="margin-1">
                viewtest
            </Link> */}
            <Link to={'/about'} className="margin-1">
                about
            </Link>
        </ul>
  </nav>
  );
}

export default Sidebar;
