import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import React, { useRef } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Sidebar from './components/Sidebar/Sidebar';
import Navbar from './components/Navbar/Navbar';
import Home from './pages/Home/Home';
import Post from './pages/Post/Post';
import Footer from './components/Footer/Footer';
import About from './pages/About/About';

function App() {
  const scrollRef = useRef(null);

  const handleScroll = (event) => {
    if (scrollRef.current) {
      scrollRef.current.scrollTop += event.deltaY;
    }
  };

  return (
    <Router>
      <div className="container-fluid">
        <div className="row" onWheel={handleScroll}>
          <Sidebar />
          <Navbar />
          <main ref={scrollRef} role="main" className="col-md-10 ml-sm-auto col-md-10 px-4 overflow-auto">
                {/* style={{height: '100vh'}}> */}
            <div className="d-sm-block d-md-none" style={{height: '5.1vh',margin: '7px'}}></div>
            <Routes>
              <Route path="/" element={<Home/>} />
              <Route path="/posts/:undecodedpostname" element={<Post/>} />
              <Route path="/about" element={<About/>} />
            </Routes>
          <Footer/>
          </main>
        </div>
      </div>

    </Router>
    
  );
}

export default App;
