import React, { useState, useEffect } from 'react';
import "./Home.css"
import { Link } from 'react-router-dom';

function Post({ item }) {
    return (
        <Link to={'/posts/'+item.title} className='nounderline'>
            <div className="post">
                <h2>{item.title}</h2>
                <p>{item.summary}</p>
                <p className='empty'/>
                <p className="date">{item.date}</p>
            </div>
        </Link>
    );
}

function PostList({ items }) {
    return (
        <div className="post-list">
        {items.map((item, index) => (
            <Post key={index} item={item} />
        ))}
        </div>
    );
}

function Home() {
    const [data, setData] = useState(null);
    document.title = "sungwoo's blog";
    
    useEffect(() => {
        const publicUrl = process.env.PUBLIC_URL;
        const filePath = publicUrl + '/data.json';
        const fetchData = async () => {
          try {
            const response = await fetch(filePath); // public 디렉토리에서 파일을 가져옴
            const jsonData = await response.json();
            setData(jsonData);
          } catch (error) {
            console.error('Error fetching data:', error);
          }
        };
    
        fetchData();
    }, []);

    return (
        <div>
            <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                <h1 className="h2">Home</h1>
            </div>
            <div>
                {data ? <PostList items={data.items} />:<p>Loading...</p>}
            </div>
        </div>
    );
}

export default Home;
