import './Sidebar.css';
import logo from '../../logo.svg';
import { Link } from 'react-router-dom';
import React from 'react';

function Sidebar() {
  return (
        <nav className="navbar col-md-2 d-none d-md-block bg-light sidebar overflow-auto" >
            <div className="sidebar-sticky">
                <ul className="nav flex-column">
                        <Link to={'/'}>
                            <img src={logo} style={{width:'90%'}} alt="Logo"/>
                        </Link>
                        <Link to={'/'} className="side-link">
                                Home
                        </Link>
                        {/* <Link to={'/posts'} className="side-link">
                                ViewExample
                        </Link> */}
                        <Link to={'/about'} className="side-link">
                                About
                        </Link>
                        <div className='side-link-icon'>
                                <Link to={'https://github.com/sungwoo925'} className="side-link-icons">
                                        <img src="/images/github.png" style={{width:'90%'}} alt="깃허브"/>
                                </Link>
                                <Link to={'https://www.instagram.com/seong_woo_0925?igsh=aXYxdW5ic3dhN2Q0'} className="side-link-icons">
                                        <img src="/images/instagram.png" style={{width:'90%'}} alt="인스타"/>
                                </Link>
                        </div>
                </ul>
            </div>
        </nav>
  );
}

export default Sidebar;
