import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import rehypeHighlight from "rehype-highlight";
import "highlight.js/styles/a11y-dark.css";
import "./Post.css"

const Post = () => {
  const [markdownContent, setMarkdownContent] = useState('');
  const [data, setData] = useState(null);
  const { undecodedpostname } = useParams(); // URL 파라미터 가져오기
  const postname = decodeURIComponent(undecodedpostname); // URL 디코딩

  useEffect(() => {
    // 파일 경로 설정
    const publicUrl = process.env.PUBLIC_URL;
    const filePath = publicUrl + '/data.json';

    function getFullLink(title) {
      console.log(data);
      for (let i = 0; i < data.items.length; i++) {
        if (data.items[i].title === title) {
          return data.items[i].dir;
        }
      }
      return null; // 파일을 찾지 못한 경우 null 반환
    }

    const fetchData = async () => {
      try {
        const response = await fetch(filePath); // public 디렉토리에서 파일을 가져옴
        const jsonData = await response.json();
        setData(jsonData);
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    // 파일 가져오기
    fetchData().then(mdfilesmaker());
    function mdfilesmaker(){
      if(data){
        fetch(getFullLink(postname))
          .then(response => {
                if (!response.ok) {
                  throw new Error('Failed to fetch markdown file');
                }
                document.title = "블로그 만들기";
                return response.text();
          })
          .then(text => {
            // Markdown을 HTML로 변환하여 상태 업데이트
            setMarkdownContent(text);
          })
          .catch(error => {
            console.error('Error fetching markdown file:', error);
          })
      }
    }
  }, []);
  useEffect(()=>{
    function getFullLink(title) {

      for (let i = 0; i < data.items.length; i++) {
        if (data.items[i].title === title) {
          return data.items[i].dir;
        }
      }
      return null; // 파일을 찾지 못한 경우 null 반환
    }
    if(data){
      fetch(getFullLink(postname))
        .then(response => {
              if (!response.ok) {
                throw new Error('Failed to fetch markdown file');
              }
              document.title = postname;
              return response.text();
        })
        .then(text => {
          // Markdown을 HTML로 변환하여 상태 업데이트
          setMarkdownContent(text);
        })
        .catch(error => {
          console.error('Error fetching markdown file:', error);
        })
    }
  },[data])

  return (
    <div className="markdown-container">
      <ReactMarkdown rehypePlugins={[rehypeHighlight]}>{markdownContent}</ReactMarkdown>
    </div>
  );
};

export default Post;
